.About {
	display: flex;
	flex-direction: row-reverse;
}

.About-contents {
	width: 90rem;
	/* border: #5cce02 1px solid; */
	margin: 4rem auto 4rem 4rem;
	font-size: 64px;
	font-weight: 600;
}
.enter-text {
	color: #5cce02;
	font-size: 68px;
}
.title {
	color: #88ffef;
}

.About-contents-main {
	display: flex;
	flex-direction: row;
}

.About-article {
	font-family: "NanumSquare", "Noto Sans KR", "sans-serif";
	font-weight: 300;
	font-size: 20px;
	margin-left: 0.5rem;
	width: 40rem;
	height: auto;
	/* border: #5cce02 1px solid; */
}

.About-detail {
	margin-left: 1rem;
	width: 48rem;
	height: 30rem;
	/* background-color: blue; */
}

.dimigo {
	color: #ff69b4;
	text-decoration: none;
}
